import "../scss/main.scss";
import lottie from "lottie-web";
import logoData from "../static/data/logo.json";
import bannerData from "../static/data/banner.json";
import deliveryData from "../static/data/delivery.json";
import bannersData from "../static/data/banners.json";
import laserData from "../static/data/laser.json";
import printData from "../static/data/print.json";

// importa todas la im√°genes de una carpeta
function importAll(r) {
  return r.keys().map(r);
}

const images = importAll(
  require.context("../static", false, /\.(png|jpe?g|webp|gif|svg|mp4)$/)
);
/*------------------------------*/
// const templateFiles = fs.readdirSync(path.resolve(__dirname, templateDir));

//-------------Menu------------------//
const showMenu = (toggleId, navId) => {
  const toggle = document.getElementById(toggleId),
    nav = document.getElementById(navId);
  if (toggle && nav) {
    toggle.addEventListener("click", () => {
      nav.classList.toggle("show");
    });
  }
};
showMenu("main-menu-toggle", "main-nav");
///////////////////////////////////////////////////////
window.addEventListener("scroll", function () {
  let header = document.querySelector("header");
  header.classList.toggle("sticky", window.scrollY > 0);
});
